// app/blog/page.js
import Header from '../../components/Header'
import Footer from '../../components/Footer'
import Link from 'next/link'

export default function BlogComingSoon() {
  return (
    <>
      <Header />
      <main className="min-h-screen bg-gradient-to-br from-[#20427f]/10 to-gray-50 flex items-center justify-center">
        <div className="max-w-2xl mx-auto px-6 text-center">
          <div className="bg-white rounded-3xl p-12 shadow-lg border border-gray-100">
            <div className="w-20 h-20 bg-[#20427f] rounded-full flex items-center justify-center text-white mx-auto mb-8">
              <svg className="w-10 h-10" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
              </svg>
            </div>
            
            <h1 className="text-4xl font-bold text-gray-900 mb-4">
              Blog Coming Soon
            </h1>
            
            <p className="text-lg text-gray-600 mb-8">
              We're crafting valuable content to help you grow your brand. 
              Stay tuned for insightful articles and expert tips.
            </p>

            <div className="space-y-4">
              <Link
                href="/"
                className="block w-full bg-[#20427f] text-white py-3 rounded-lg hover:bg-[#1a3566] transition-colors duration-200 font-medium"
              >
                Back to Home
              </Link>
              
              <Link
                href="/contact"
                className="block w-full border border-gray-300 text-gray-700 py-3 rounded-lg hover:bg-gray-50 transition-colors duration-200 font-medium"
              >
                Get in Touch
              </Link>
            </div>
          </div>
        </div>
      </main>
      <Footer />
    </>
  )
}

export const metadata = {
  title: 'Blog Coming Soon - YourZerosAndOnes',
  description: 'Our blog is coming soon with valuable insights about branding and design',
}